import {expressionsPlugin, randomString, svgNS} from "../utils/common";
import BaseRenderer from "./BaseRenderer";
import FontManager from "../utils/FontManager";
import SVGBaseElement from "../elements/svgElements/SVGBaseElement";
import ShapeElement from "../elements/ShapeElement";
import SVGTextElement from "../elements/svgElements/SVGTextElement";
import ImageElement from "../elements/ImageElement";
import CompElement from "../elements/CompElement";
import SolidElement from "../elements/SolidElement";


export default class SVGRenderer extends BaseRenderer
{
	constructor (animationItem, config)
	{
		super();
		this.animationItem = animationItem;
		this.layers = null;
		this.renderedFrame = -1;
		this.globalData = {
			frameNum: -1
		};
		this.renderConfig = {
			preserveAspectRatio: (config && config.preserveAspectRatio) || 'xMidYMid meet',
			progressiveLoad: (config && config.progressiveLoad) || false
		};
		this.elements = [];
		this.pendingElements = [];
		this.destroyed = false;

	}

	createBase (data)
	{
		return new SVGBaseElement(data, this.layerElement, this.globalData, this);
	}

	createShape (data)
	{
		return new ShapeElement(data, this.layerElement, this.globalData, this);
	}

	createText (data)
	{
		return new SVGTextElement(data, this.layerElement, this.globalData, this);

	}

	createImage (data)
	{
		return new ImageElement(data, this.layerElement, this.globalData, this);
	}

	createComp (data)
	{
		return new CompElement(data, this.layerElement, this.globalData, this);

	}

	createSolid (data)
	{
		return new SolidElement(data, this.layerElement, this.globalData, this);
	}

	configAnimation (animData)
	{
		this.layerElement = document.createElementNS(svgNS, 'svg');
		this.layerElement.setAttribute('xmlns', 'http://www.w3.org/2000/svg');
		this.layerElement.setAttribute('width', animData.w);
		this.layerElement.setAttribute('height', animData.h);
		this.layerElement.setAttribute('viewBox', '0 0 ' + animData.w + ' ' + animData.h);
		this.layerElement.setAttribute('preserveAspectRatio', this.renderConfig.preserveAspectRatio);
		this.layerElement.style.width = '100%';
		this.layerElement.style.height = '100%';
		//this.layerElement.style.transform = 'translate3d(0,0,0)';
		//this.layerElement.style.transformOrigin = this.layerElement.style.mozTransformOrigin = this.layerElement.style.webkitTransformOrigin = this.layerElement.style['-webkit-transform'] = "0px 0px 0px";
		this.animationItem.wrapper.appendChild(this.layerElement);
		//Mask animation
		var defs = document.createElementNS(svgNS, 'defs');
		this.globalData.defs = defs;
		this.layerElement.appendChild(defs);
		this.globalData.getAssetData = this.animationItem.getAssetData.bind(this.animationItem);
		this.globalData.getAssetsPath = this.animationItem.getAssetsPath.bind(this.animationItem);
		this.globalData.progressiveLoad = this.renderConfig.progressiveLoad;
		this.globalData.frameId = 0;
		this.globalData.nm = animData.nm;
		this.globalData.compSize = {
			w: animData.w,
			h: animData.h
		}
		this.data = animData;
		this.globalData.frameRate = animData.fr;
		var maskElement = document.createElementNS(svgNS, 'clipPath');
		var rect = document.createElementNS(svgNS, 'rect');
		rect.setAttribute('width', animData.w);
		rect.setAttribute('height', animData.h);
		rect.setAttribute('x', 0);
		rect.setAttribute('y', 0);
		var maskId = 'animationMask_' + randomString(10);
		maskElement.setAttribute('id', maskId);
		maskElement.appendChild(rect);
		var maskedElement = document.createElementNS(svgNS, 'g');
		maskedElement.setAttribute("clip-path", "url(#" + maskId + ")");
		this.layerElement.appendChild(maskedElement);
		defs.appendChild(maskElement);
		this.layerElement = maskedElement;
		this.layers = animData.layers;
		this.globalData.fontManager = new FontManager();
		this.globalData.fontManager.addChars(animData.chars);
		this.globalData.fontManager.addFonts(animData.fonts, defs);
		this.elements = Array.apply(null, {length: animData.layers.length});
	}


	destroy ()
	{
		this.animationItem.wrapper.innerHTML = '';
		this.layerElement = null;
		this.globalData.defs = null;
		var i, len = this.layers ? this.layers.length : 0;
		for (i = 0; i < len; i++)
		{
			if (this.elements[i])
			{
				this.elements[i].destroy();
			}
		}
		this.elements.length = 0;
		this.destroyed = true;
		this.animationItem = null;
	}

	updateContainerSize ()
	{
	}

	buildItem (pos)
	{
		var elements = this.elements;
		if (elements[pos] || this.layers[pos].ty == 99)
		{
			return;
		}
		elements[pos] = true;
		var element = this.createItem(this.layers[pos]);

		elements[pos] = element;
		if (expressionsPlugin)
		{
			if (this.layers[pos].ty === 0)
			{
				this.globalData.projectInterface.registerComposition(element);
			}
			element.initExpressions();
		}
		this.appendElementInPos(element, pos);
		if (this.layers[pos].tt)
		{
			if (!this.elements[pos - 1] || this.elements[pos - 1] === true)
			{
				this.buildItem(pos - 1);
				this.addPendingElement(element);
			}
			else
			{
				element.setMatte(elements[pos - 1].layerId);
			}
		}
	}

	checkPendingElements ()
	{
		while (this.pendingElements.length)
		{
			var element = this.pendingElements.pop();
			element.checkParenting();
			if (element.data.tt)
			{
				var i = 0, len = this.elements.length;
				while (i < len)
				{
					if (this.elements[i] === element)
					{
						element.setMatte(this.elements[i - 1].layerId);
						break;
					}
					i += 1;
				}
			}
		}
	}

	renderFrame (num)
	{
		if (this.renderedFrame == num || this.destroyed)
		{
			return;
		}
		if (num === null)
		{
			num = this.renderedFrame;
		}
		else
		{
			this.renderedFrame = num;
		}
		//clearPoints();
		/*console.log('-------');
		 console.log('FRAME ',num);*/
		this.globalData.frameNum = num;
		this.globalData.frameId += 1;
		this.globalData.projectInterface.currentFrame = num;
		var i, len = this.layers.length;
		if (!this.completeLayers)
		{
			this.checkLayers(num);
		}
		for (i = len - 1; i >= 0; i--)
		{
			if (this.completeLayers || this.elements[i])
			{
				this.elements[i].prepareFrame(num - this.layers[i].st);
			}
		}
		for (i = len - 1; i >= 0; i--)
		{
			if (this.completeLayers || this.elements[i])
			{
				this.elements[i].renderFrame();
			}
		}
	}

	appendElementInPos (element, pos)
	{
		var newElement = element.getBaseElement();
		if (!newElement)
		{
			return;
		}
		var i = 0;
		var nextElement;
		while (i < pos)
		{
			if (this.elements[i] && this.elements[i] !== true && this.elements[i].getBaseElement())
			{
				nextElement = this.elements[i].getBaseElement();
			}
			i += 1;
		}
		if (nextElement)
		{
			this.layerElement.insertBefore(newElement, nextElement);
		}
		else
		{
			this.layerElement.appendChild(newElement);
		}
	}

	hide ()
	{
		this.layerElement.style.display = 'none';
	}

	show ()
	{
		this.layerElement.style.display = 'block';
	}

	searchExtraCompositions (assets)
	{
		var i, len = assets.length;
		var floatingContainer = document.createElementNS(svgNS, 'g');
		for (i = 0; i < len; i += 1)
		{
			if (assets[i].xt)
			{
				var comp = this.createComp(assets[i], floatingContainer, this.globalData.comp, null);
				comp.initExpressions();
				//comp.compInterface = CompExpressionInterface(comp);
				//Expressions.addLayersInterface(comp.elements, this.globalData.projectInterface);
				this.globalData.projectInterface.registerComposition(comp);
			}
		}
	}
}


